import React, { useState } from "react";
import Header from "./components/Header";
import "./components/EmojiContainer";
import EmojiContainer from "./components/EmojiContainer";
import data from "./components/json/emoji.json";

export default function App() {
  const [emojisList, setEmojisList] = useState(data);


  const searchEmojis = (e) => {
    console.log(e.target.value);
    let searchEmojis = data.filter((emoji) =>
      emoji.keywords.includes(e.target.value)
    );
    setEmojisList(searchEmojis);
  };

  return (
    <div style={{backgroundColor:"rgb(206, 206, 171)"}}>
      <Header />
      <div style={{ textAlign: "center" }}>
        <input
          type="text"
          name="search"
          onChange={searchEmojis}
          style={{
            display: "inline-block",
            borderRadius: "10px",
            width: "300px",
            padding: "10px",
            outline: "none",
          }}
        />
      </div>
      <EmojiContainer emojisList={emojisList} />
    </div>
  );
}
