import React from "react";
import GitHubButton from "react-github-btn";

export default function Header() {
  return (
    <div style={{ textAlign: "center",paddingTop:"100px", fontFamily:"monospace" }}>
      <h1>
        
        React search Emoji<span>&#x1f984;</span>
      </h1>
      <p style={{fontSize:"20px"}}>Simple React tool for search Emoji with ReactJS </p>
      <p>
        <GitHubButton
          href="https://github.com/Wais000"
          data-show-count={true}
          data-size="large"
          data-text="star"
          data-icon="octicon-star"
        >
          
          star
        </GitHubButton>
        <GitHubButton
          href="https://github.com/Wais000"
          data-show-count={true}
          data-size="large"
          data-text="form"
          data-icon="octicon-repo-forked"
        >
          
        fork
        </GitHubButton>
      </p>
    </div>
  );
}
